import React, { useEffect, useState } from "react";
import { useTransition, animated } from 'react-spring'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEdit } from '@fortawesome/free-regular-svg-icons';
import Notification from "./Notification";
import { ThemeContext } from "../Utilities/ThemeContext";

const firebase = require('firebase');

function FloatingDialog(props){ //usersData, selectedUser, exercise (key)
    const { theme, setTheme } = React.useContext(ThemeContext);

    const [show, setShow] = useState(false);
    const [max, setMax] = useState(0);
    const [work, setWork] = useState(0);
    const [label, setLabel] = useState("");

    useEffect(() => {
        setMax(props.exMax);
        setWork(props.exWork);
        setLabel(props.exlabel);
    }, [props])

    //React spring
    const menuTransition = useTransition(show, null, {
        from: { opacity: 0},
        enter: { opacity: 1},
        leave: { opacity: 0},
    })

    //function on button submit
    function onSubmit(){
        const db = firebase.firestore();
        //get initial data of exerscise for selected member
        if(props.usersData !== undefined){
            const memberData = props.usersData.data.filter(member => {
                return member.id === props.selectedUser;
            });

            let isExerciseInside = false;

            if(memberData.length > 0){
                let tmpExData = memberData[0].exercisesData;
                tmpExData.forEach(element => {
                    if (element.key === props.exercise){
                        isExerciseInside = true;
                        element.work = work;
                        element.max = max;
                    }
                })
                
                if(!isExerciseInside){
                    tmpExData.push({ key : props.exercise, max: max, work: work });
                }

                //Update record in database
                db.collection("users").doc(props.selectedUser).update({ exercisesData : tmpExData })
                .then(function (){
                    Notification("Z치znam byl upraven.", false)
                })
                .catch(function(error) {
                    console.log(error); 
                    Notification("Chyba", true);
                });

                //Close dialog
                setShow(false);
            }
        }
    }

    return (
        <>
            <span>
                <FontAwesomeIcon role="img" aria-label="fntawsm" className={`text-${theme}-tsec hover:text-${theme}-tpr cursor-pointer`} icon={ faEdit } onClick={() => setShow(!show)}/>
            </span>

            { /* Menu transition and content */ }
            {
                menuTransition.map(({ item, key, props }) =>
                    item && 
                    <animated.div 
                        key={key} 
                        style={props} 
                        className={`fixed bg-black-t-30 top-0 left-0 w-full h-auto z-50`}
                        // onClick={() => setShow(!show)}
                    >
                        <div className={`flex h-screen`}>
                            <div className={`m-auto`}>
                                <div className={`w-64 sm:w-64 md:w-64 bg-${theme}-cardbg rounded-lg shadow-md m-1 sm:m-1 md:m-6 text-center`}>

                                    <h2 className={`py-6 text-xl text-${theme}-tpr`}>{label}</h2>
                                    {/* <h2 className={`py-1 text-lg text-${theme}-tpr`}>dsad</h2> */}

                                    <label className={`w-full text-${theme}-tsec`}>
                                        Pracovn칤 v치ha
                                    </label>
                                    <div className={`py-3`}>
                                        <input className={`font-bold text-xl text-center bg-${theme}-cardbg text-${theme}-tpr`} 
                                                type="number" id="workingWeight" name="workingWeight" min="0" max="300" 
                                                step="0.5" value={work} onChange={(e) => setWork(e.target.value)} />
                                    </div>

                                    <label className={`w-full text-${theme}-tsec pt-5`}>
                                        Maxim치ln칤 v치ha
                                    </label>
                                    <div className={`py-3`}>
                                        <input className={`font-bold text-xl text-center bg-${theme}-cardbg text-${theme}-tpr`} 
                                                type="number" id="maxWeight" name="maxWeight" min="0" max="300" 
                                                step="0.5" value={max} onChange={(e) => setMax(e.target.value)} />
                                    </div>
                                    <div className={`flex justify-center w-full`}>
                                        <button onClick={() => setShow(!show)} type="submit" className={`w-1/2 text-${theme}-tpr rounded-lg shadow-xl border p-3 m-3 mt-12`}>Storno</button>
                                        <button onClick={() => onSubmit()} type="button" className={`rounded-lg font-bold bg-${theme}-sec text-white shadow-xl border border-${theme}-cardbg p-3 m-3 mt-12 w-1/2`}>Ulo쬴t</button>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </animated.div>
                )
            }
        </>
    )
}

export default FloatingDialog;